/*----------------------------------------*\
      #animations
\*----------------------------------------*/

//  360deg rotation

@keyframes rotation {
  to {
    transform: rotate(360deg);
  }
}

// reverse 360deg rotation

@keyframes reverseRotation {
  to {
    transform: rotate(-360deg);
  }
}

// shake

@keyframes shake {
  0% {
    transform: translate(-50%, -50%) translate3d(0px, 0, 0);
  }
  10% {
    transform: translate(-50%, -50%) translate3d(1px, 0, 0);
  }
  20% {
    transform: translate(-50%, -50%) translate3d(2px, 0, 0);
  }
  30% {
    transform: translate(-50%, -50%) translate3d(3px, 0, 0);
  }
  40% {
    transform: translate(-50%, -50%) translate3d(4px, 0, 0);
  }
  50% {
    transform: translate(-50%, -50%) translate3d(5px, 0, 0);
  }
  60% {
    transform: translate(-50%, -50%) translate3d(4px, 0, 0);
  }
  70% {
    transform: translate(-50%, -50%) translate3d(3px, 0, 0);
  }
  80% {
    transform: translate(-50%, -50%) translate3d(3px, 0, 0);
  }
  90% {
    transform: translate(-50%, -50%) translate3d(2px, 0, 0);
  }
  100% {
    transform: translate(-50%, -50%) translate3d(0px, 0, 0);
  }
}
