@use "../abstracts" as abs;
@use "../base/animations";

/*----------------------------------------*\
      #loader
\*----------------------------------------*/

.loader {
  visibility: hidden;
  transform: scale(0);
  width: 9rem;
  height: 9rem;

  &--active {
    @extend .loader;
    visibility: visible;
    transform: scale(1) translate(-50%, -50%);
    .loader-wrapper {
      animation: rotation 2s linear infinite;
    }
    .loader__text {
      animation: reverseRotation 2s linear infinite;
    }
  }

  &-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__text {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    line-height: 9rem;
  }

  &__circle {
    position: absolute;
    width: 100%;
    height: 100%;
    border-top: 1rem solid;
    border-radius: 50%;

    &--one {
      border-color: #e76f51;
    }

    &--two {
      border-color: #e9c46a;
      transform: rotate(120deg);
    }
    &--three {
      border-color: #264653;
      transform: rotate(240deg);
    }
  }
}
